import React from 'react';
import './Projects.css';

const ProjectCard = (e) => {

    const pTitle = e.e.projectTitle;
    const pTechStack = e.e.projectTechStack;
    const pIntro = e.e.description;
    const pImg = e.e.image;

    return (
        <div className="project_card">
            <div className="project_card_head">
                <div className="project_card_title">
                    <p>{pTitle}</p>
                </div>
                <div className="project_card_tech_stack">
                    <p>{pTechStack}</p>
                </div>
            </div>
            <div className="project_card_body">
                <img src={pImg} alt="project_image" />
            </div>
            <div className="project_card_footer">
                <p>{pIntro}</p>
            </div>
        </div>
    )
}

export default ProjectCard;