import School from "@mui/icons-material/School";
import Work from "@mui/icons-material/Work";
import Link from "@mui/icons-material/Link";
import Pdf from "@mui/icons-material/PictureAsPdfSharp";

const Experience = (e) => {
  console.log(e);
  return (
    <div className="experience_card">
      <div className="experience_card__header">
        <div className="experience_card__header__left">
          <p>
            {" "}
            <Work /> {e.e.company_name}
          </p>
          <p> {e.e.designation} </p>
        </div>
        <div className="experience_card__header__right">
          <p>
            {" "}
            {e.e.start_date} - {e.e.end_date}{" "}
          </p>
        </div>
      </div>
      <div className="experience_card__body">
        <ul>
          {e.e.desc.map((item, index) => {
            return <li key={index}> {item} </li>;
          })}
        </ul>
      </div>
    </div>
  );
};

const Education = (e) => {
  return (
    <div className="education_card">
      <div className="education_card__left">
        <p>
          {" "}
          <School /> {e.e.college_name}
        </p>
        <span> {e.e.study} </span>
      </div>
      <div className="education_card__right">
        <p>
          {" "}
          {e.e.start_year} - {e.e.end_year}{" "}
        </p>
      </div>
    </div>
  );
};

const Course = (e) => {
  return (
    <div className="course_card">
      <span className="course_name"> {e.e.course_name} </span>-{" "}
      <a href={e.e.link} target="_blank" rel="noreferrer">
        {" "} <br/>
        {e.e.platform}{" "}
      </a> 
      : <br/>
      <span className="course_topics"> {e.e.topics} </span>
    </div>
  );
};

const Coding = (e) => {
  return (
    <div className="coding_card">
      <a href={e.e.link} target="_blank" rel="noreferrer">
        {" "}
        {e.e.name}{" "}
      </a>
    </div>
  );
};

