import "./Contact.css";
import { LinkedIn, GitHub, Instagram, Twitter, Facebook, Email } from '@mui/icons-material';

const Contact = () => {

  const social = [
    { "name": "Github", "link": "https://www.github.com/mnk17arts", "class": GitHub },
    { "name": "Linkedin", "link": "https://www.linkedin.com/in/mnk17arts/", "class": LinkedIn},
    { "name": "Instagram", "link": "https://www.instagram.com/nitish.kumar.m/", "class": Instagram },
    { "name": "Twitter", "link": "https://twitter.com/MadhapurK", "class": Twitter },
    { "name": "Facebook", "link": "https://www.facebook.com/mnk17arts", "class": Facebook },
    { "name": "Email", "link": "mailto:mnk17arts@gmail.com", "class": Email },
  ];


  return (
    <div className="contact">

      <div className="contact-container">

        <div className="contact-container-top">
          <h3>CONTACT ME</h3>
          <h2> Ways to contact ME </h2>
          <p>Let's bring your digital dreams to fruition and leave a lasting impression on your audience. Feel free to contact me.</p>
        </div>

        <div className="contact-container-bottom">

          <div className="socials">
            {social.map((item, index) => {
              return (
                <div className="socials-item" key={index}><item.class className="socials-item-icon" />
                  <a href={item.link} target="_blank" rel="noreferrer"> 
                  
                    
                   <span>{item.name}</span></a>
                </div>
              );
            })}
          </div>

        </div>

      </div>

    </div>
  );
}

export default Contact;
