import React, {useState} from 'react';
import './Projects.css';
import { GitHub, Web } from '@mui/icons-material';
import ReactMarkdown from 'react-markdown';
import Share from './Share';


const Project = (e) => {
    const mdUrl = e.projectData.mdLink ? e.projectData.mdLink : null;
    const [proDesc, setProDesc] = useState();
    fetch(mdUrl).then((response) => response.text()).then((text) => {
        setProDesc(text);
    });



    const githubLink = e.projectData.githubLink ? e.projectData.githubLink : null;
    const liveLink = e.projectData.liveLink ? e.projectData.liveLink : null;
    const projectTitle = e.projectData.projectTitle ? e.projectData.projectTitle : null;
    const projectTechStack = e.projectData.projectTechStack ? e.projectData.projectTechStack : null;
    const projectImage = e.projectData.image ? e.projectData.image : null;
    const desc=e.projectData.description ? e.projectData.description : "mnk17arts' project";

    

    return (
        <div className="project__container">
            <div className="project_div">
                <div className="project_head">
                    <div className="project_title">
                        <h1>{projectTitle ? projectTitle : "#ProjectTitle"}</h1>
                    </div>
                    <div className="project_tech_stack"> 
                        <h3>{projectTechStack ? projectTechStack : "#ts1 #ts2 #ts3"}</h3>
                    </div>
                    <div className="project_links">
                        {githubLink ? <a href={githubLink} target="_blank" rel="noreferrer"><GitHub /></a> : null}
                        {liveLink ? <a href={liveLink} target="_blank" rel="noreferrer"><Web /></a> : null}
                    </div>      
                </div>
                <div className="project_media">
                    <div className="project_media_image">
                        <img src={projectImage} alt="project_image" />
                    </div>
                </div>
                <div className="project_body">  
                    <ReactMarkdown children={proDesc} />
                
                </div>
                <div className="project_footer">  </div>

                <div className="project_share"> 
                    <Share title={projectTitle} url={liveLink ? liveLink : githubLink} info={desc} hashs={projectTechStack} />
                </div>
            </div>
        </div>
    );

}

export default Project;