import "./Projects.css";
import ProjectBox from "./ProjectBox";

const Projects = (e) => {
    const data = e.projectData;
    return (
        <div className="projects_container">
            {
                data.map((e) => {
                    return (
                        <ProjectBox projectData={e} />
                    )
                })
            }
        </div>
    );
}

export default Projects;
