import Home from './Components/Home/Home';
import Navbar from './Components/Navbar/MainNavigation';
import Resume from './Components/Resume/Resume';
import Projects from './Components/Projects/Projects';
import Project from './Components/Projects/Project';
import Contact from './Components/Contact/Contact';
import Loader from './Components/Loader/Loader';
import { useState, useEffect } from 'react';
// eslint-disable-next-line
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';


function App() {
  const [loading, setLoading] = useState(true);
  const [projectData, setData] = useState([]);
  useEffect(() => { 
    setTimeout(() => {
      setLoading(false);
    }, 3000);
  }, []);

  useEffect(() => {
    fetch('https://raw.githubusercontent.com/mnk17arts/data/main/JSON/data.json').then((res) => {
      return res.json();
    }
    ).then((data) => {
      setData(data.projectData);
    }
    )
  }, []);


  return (
    <div className="App">
      {loading ? <Loader /> : null}
        {/* <Mode /> */}
            <Navbar />
            <Routes>
                <Route exact path="/"  element={<Home />} />
                <Route path="/resume" element={<Resume />} />
                <Route path="/projects" 
                  element={
                    <Projects
                      projectData={projectData}
                    />  
                  }
                />
                {
                    projectData.map((e) => {
                        return (
                            e.data.map((f) => {
                                return (
                                    <Route path={`/projects/${f.url}`} element={<Project projectData={f} />} /> 
                                      )
                                })
                              )
                    })
                }
                <Route path="/contact" element={<Contact />} />
            </Routes>
    </div>
  );
}
 
export default App;
