import React from "react";
import { NavLink } from "react-router-dom";

import "./NavLinks.css";

const NavLinks = (props) => {
    return (
      <ul className="nav-links">
        <li>
          <NavLink to="/" exact className="bb-slider">
            Home
          </NavLink>
        </li>
        <li>
          <NavLink to="/resume" className="bb-slider">
            About
          </NavLink>
        </li>
        <li>
          <NavLink to="/projects" className="bb-slider">
            Projects
          </NavLink>
        </li>
        <li>
          <NavLink to="/contact" className="bb-slider">
            Contact
          </NavLink>
        </li>
      </ul>
    );
};

export default NavLinks;