import React, {useState} from 'react';
import { Link} from 'react-router-dom';
import ProjectCard from './ProjectCard';
import { ArrowDownward, ArrowUpward } from '@mui/icons-material';
import './Projects.css';

const ProjectBox = (props) => {

    const [showMore, setShowMore] = useState({
        show: false,
        items: 3
    });

    const btitle = props.projectData.type;
    const bdesc = props.projectData.desc;
    const data = props.projectData.data;
    const proLength = data.length;

    const showMoreItems = () => {
        if(showMore.show) {
            setShowMore({
                show: false,
                items: 3
            });
            return;
        }
        setShowMore({
            show: true,
            items: proLength
        });
    }


    return (
        <div className="projects_ele">
            <div className="projects_ele_title">
                <p> {btitle} </p>
                <span> {bdesc} </span>
            </div>


            <div className="projects_ele_body">
                {data.slice(0, showMore.items).map((e) => {
                    return (
                        <Link to={`/projects/${e.url}`}>
                            <ProjectCard e={e} />
                        </Link>
                    )
                })}
            </div>

            <div className="projects_ele_footer">
                <div className="show_more" onClick={showMoreItems}>

                    {showMore.show ? "Show Less " : "Show More "}
                    {showMore.show ? <ArrowUpward /> : <ArrowDownward />}
                </div>
            </div>
        </div>
    )
}

export default ProjectBox;