import React, {useState} from 'react';
import Lii from '@mui/icons-material/LinkedIn';
import Wai from '@mui/icons-material/WhatsApp';
import Ti from '@mui/icons-material/Twitter';
import Ei from '@mui/icons-material/Email';
import Fbi from '@mui/icons-material/Facebook';
import Si from '@mui/icons-material/ShareRounded';
import X from '@mui/icons-material/Close';
import {
    FacebookShareButton,
    LinkedinShareButton,
    TwitterShareButton,
    WhatsappShareButton,
    EmailShareButton,
} from "react-share";
import './Projects.css';

const Share = (props) => {
    const [shareOn, setShareOn] = useState(false);

    const { title, url, info, hashs } = props;

    const hash = hashs ? hashs.split(',') : null;
    return (
        <div className="share__container">
            <div className="share_div">
                <div className="share_head" 
                onClick={() => setShareOn(!shareOn)}
                >
                     {shareOn ? <X /> : <Si/> } 
                    <p>Share</p>
                </div>
                <div className={`share_body ${shareOn ? "shareOn" : ''}`}>
                    <div className="share_body_icons">
                        <FacebookShareButton url={url} quote={info + title}>
                            <Fbi />
                        </FacebookShareButton>
                        <LinkedinShareButton url={url} title={info + title}>
                            <Lii />
                        </LinkedinShareButton>
                        <TwitterShareButton url={url} title={info + title} hashtags={[hash, "robotica"]} >
                            <Ti />
                        </TwitterShareButton>
                        <WhatsappShareButton url={"_" + info + "_ " + url} title={title}>
                            <Wai />
                        </WhatsappShareButton>
                        <EmailShareButton url={url} subject={title} body={info} >
                            <Ei />
                        </EmailShareButton>
                    </div>
                </div>
                <div className="share_footer"> </div>
            </div>
        </div>
    );
}   

export default Share;