import "./Home.css";
import ShortCards from "../ShortCards/ShortCards";
import { Link } from "react-router-dom";
import mnk from "../img/mnk.png";

const Home = () => {
  const social = [
    {
      name: "Github",
      link: "https://github.com/mnk17arts",
    },
    {
      name: "Linkedin",
      link: "https://www.linkedin.com/in/mnk17arts/",
    },
    {
      name: "Codepen",
      link: "https://codepen.io/mnk17arts",
    }
  ];



  return (
    <div className="home">
      
      <div className="det">
        <div className="det-name">
                <p>Madhapur <br/>
                  <span className="odot">N</span>itish <br/>
                  Kumar
                  <span className="odot" >.</span>
                </p>
        </div>
        <div className="det-social">
          {social.slice(0, 3).map((item, index) => {
            return (
              <div className="det-social-item" key={index}>
                <a href={item.link} target="_blank" rel="noreferrer">
                  {item.name}
                </a>
              </div>
            );
          })}
        </div>
        <div className="det-contact">
            <p>
              < Link to="/contact">CONTACT ME</Link>
            </p>
        </div>
      </div>
      <div className="img">
        <img className="mnk-img" src={mnk} alt="" ></img>
      </div>
      <div className="intro">
        <p className="intro-heading">Introduction</p>
        <p className="intro-role">
          Frontend Web and Software Developer
        </p>
        <p className="intro-desc">
          As a skilled developer, I craft elegant solutions and build dynamic websites that bring ideas to life.From HTML to JavaScript and everything in between, I can help turn your vision into a pixel-perfect reality.
        </p>
        <p className="learn-more">
          < Link to="/resume">See More &darr; </Link>
        </p>
      </div>

      {/* <ShortCards /> */}
    </div>
  ); 
}

export default Home;